@mixin placeholder($color) {
  &::-webkit-input-placeholder {
      color: $color;
  }

  &::-moz-placeholder {
      color: $color;
  }

  &:-moz-placeholder {
      color: $color;
  }

  &:-ms-input-placeholder {
      color: $color;
  }
}


@mixin ios-text-field-fix ($isBorderRadius: true) {
  @if not $isBorderRadius {
    border-radius: 0; 
  }
   -webkit-appearance: none;
}


@mixin svg {
  svg {
    max-width: 100%;
    max-height: 100%;
  }
}


@mixin img {
  img {
    max-width: 100%;
    max-height: 100%;
  }
}